import Vue from 'vue'
import VueRouter from 'vue-router'

/* Layout */
import Layout from '@/layout'

Vue.use(VueRouter)

/**
 * constantRoutes
 * 基本页没有权限要求
 * 所有角色可以访问
 */
export const constantRoutes = [
  {
    path: '/redirect',
    component: Layout,
    hidden: true,
    children: [
      {
        path: '/redirect/:path(.*)',
        component: () => import('@/views/redirect/index')
      }
    ]
  },

  {
    path: '/login',
    name: 'login',
    hidden: true,
    component: () => import(/* webpackChunkName: "login" */ '../views/login')
  },

  {
    path: '/',
    component: Layout,
    redirect: '/home',
    children: [
      {
        path: 'home',
        component: () => import('@/views/home/home'),
        name: 'Home',
        meta: { title: '首页', icon: 'el-icon-s-home', affix: true }
      }
    ]
  },

  {
    path: '/404',
    component: () => import('@/views/error-page/404'),
    hidden: true
  },
  {
    path: '/401',
    component: () => import('@/views/error-page/401'),
    hidden: true
  },

  // // 404页必须放在最后 !!!
  // { path: '*', redirect: '/404', hidden: true }
]

/**
 * asyncRoutes
 * 需要根据用户角色动态加载的路由
 */
export const asyncRoutes = [
  {
    path: '/point',
    component: Layout,
    redirect: '/point/page',
    alwaysShow: true, // 始终显示根目录
    name: 'Point',
    meta: {
      title: '要点',
      icon: 'el-icon-lock',
      roles: ['admin']
    },
    children: [
      {
        path: 'mini',
        component: () => import('@/views/point/mini'),
        name: 'Mini',
        meta: {
          title: '微信小程序',
          roles: ['admin']
        }
      },
      {
        path: 'regular ',
        component: () => import('@/views/point/regular'),
        name: 'Regular',
        meta: {
          title: '正则表达式',
          roles: ['admin']
        }
      },
      {
        path: 'css ',
        component: () => import('@/views/point/css'),
        name: 'Css',
        meta: {
          title: 'css',
          roles: ['admin']
        }
      },
      {
        path: 'es ',
        component: () => import('@/views/point/es'),
        name: 'Es',
        meta: {
          title: 'es',
          roles: ['admin']
        }
      },
      {
        path: 'git ',
        component: () => import('@/views/point/git'),
        name: 'Git',
        meta: {
          title: 'git',
          roles: ['admin']
        }
      },
      {
        path: 'js ',
        component: () => import('@/views/point/js'),
        name: 'Js',
        meta: {
          title: 'js',
          roles: ['admin']
        }
      },
    ]
  },

  {
    path: '/error',
    component: Layout,
    redirect: 'noRedirect',
    name: 'ErrorPages',
    meta: {
      title: 'Error Pages',
      icon: 'el-icon-error'
    },
    children: [
      {
        path: '401',
        component: () => import('@/views/error-page/401'),
        name: 'Page401',
        meta: { title: '401', noCache: true }
      },
      {
        path: '404',
        component: () => import('@/views/error-page/404'),
        name: 'Page404',
        meta: { title: '404', noCache: true }
      }
    ]
  },

  // 404页必须放在最后 !!!
  { path: '*', redirect: '/404', hidden: true }
]

const createRouter = () => new VueRouter({
  mode: 'history',
  base: process.env.BASE_URL,
  routes: constantRoutes
})

const router = createRouter()

// 详细请看 : https://github.com/vuejs/vue-router/issues/1234#issuecomment-357941465
export function resetRouter() {
  const newRouter = createRouter()
  router.matcher = newRouter.matcher // 重置路由
}

export default router
