import router, { resetRouter } from '@/router'

const state = {
  roles: [],
  name: '',
  avatar: '',
  introduction: '',
}

const mutations = {
  SET_ROLES: (state, roles) => {
    state.roles = roles
  },
  SET_NAME: (state, name) => {
    state.name = name
  },
  SET_AVATAR: (state, avatar) => {
    state.avatar = avatar
  },
}

const actions = {
  // 删除token
  resetToken({ commit }) {
    return new Promise(resolve => {
      commit('SET_ROLES', [])
      localStorage.removeItem('token')
      resolve()
    })
  },

  // 获取用户信息
  getInfo({ commit, state }) {
    return new Promise((resolve, reject) => {
      const roles = ['admin']
      const name = '总管理员'
      const avatar = 'https://ss1.bdstatic.com/70cFuXSh_Q1YnxGkpoWK1HF6hhy/it/u=3277870098,1272922089&fm=26&gp=0.jpg'

      commit('SET_ROLES', roles)
      commit('SET_NAME', name)
      commit('SET_AVATAR', avatar)
      resolve({ roles: roles })
    })
  },

  // 退出登录
  logout({ commit, state, dispatch }) {
    return new Promise((resolve, reject) => {
      commit('SET_ROLES', [])
      localStorage.removeItem('token')
      resetRouter()

      // 重置访问的视图和缓存的视图
      // to fixed https://github.com/PanJiaChen/vue-element-admin/issues/2485
      dispatch('tagsView/delAllViews', null, { root: true })

      resolve()
    })
  },
}

export default {
  namespaced: true,
  state,
  mutations,
  actions
}
